import { setWatermarkConfig } from "./setWatermarkConfig";
import { IAppConfig, IRotateConfig, IWatermarkConfig } from "./types";

class AppConfig {
  /**
   * 
   */
  _url: string;

  /**
   * ID
   *  documentId 
   */
  _documentId: string;

  /**
   * 
   */
  _previewWatermark: IWatermarkConfig | null;

  /**
   * iSignature
   */
  // iSignatureAPI: any;

  constructor(userConfig: IAppConfig) {
    // 
    // this.iSignatureAPI = new iSignatureAPI();

    const { url = "", documentId = "", previewWatermark = null } = userConfig;
    this._url = url;
    this._documentId = documentId;
    this._previewWatermark = null;
    if (previewWatermark) {
      this._previewWatermark = setWatermarkConfig(previewWatermark);
    }
  }

  /**
   * 
   */
  get previewWatermark() {
    return this._previewWatermark;
  }

  /**
   * 
   */
  set url(url: string) {
    this._url = url;
  }

  /**
   * 
   */
  get url() {
    return this._url;
  }

  /**
   * ID
   */
  set documentId(documentId: string) {
    this._documentId = documentId;
  }

  /**
   * ID
   */
  get documentId() {
    return this._documentId;
  }
}

/**
 * 
 */
const userConfig: IAppConfig = (<any>window).setAppConfig();

/**
 * 
 */
const appConfig = new AppConfig(userConfig);
(<any>window).KGPdfViewerWebApp = (<any>window).appConfig = appConfig;

export type { IAppConfig };
export default appConfig;
